<template>
  <div>  
    <form @submit.prevent="returnBook">
      <label>
        Book ID:
        <input v-model="bookid" type="text" required />
      </label>

      <label>
        user ID:
        <input v-model="userid" type="text" required />
      </label>

      <button type="submit">return Book</button>
    </form>
  </div>
</template>

<script>
import "whatwg-fetch";

export default {
  data() {
    return {
      bookid: "",
      userid: "",
    };
  },
  methods: {/*
    returnBook() {
      console.log("returnBook,", "bookid:", this.bookid, "userid:", this.userid);
      //fetch(`http://localhost:8080/staff/material/return/${this.bookid}/${this.userid}`, {
        fetch(`/staff/material/return/${this.bookid}/${this.userid}`, {
        method: "PUT",
      })
        .then((response) => {
          // 处理响应数据
          alert('Book returned successfully!');
          console.log("成功了", response);
        })
        .catch((error) => {
          // 处理错误
          console.error("失败了", error);
          alert('Failed to return book.');
        });
    },*/
    async returnBook() {
      console.log("returnBook,", "bookid:", this.bookid, "userid:", this.userid);
      const url = `http://localhost:8080/staff/material/return/${this.bookid}/${this.userid}`
      const response = await fetch(url, { method:  'PUT' });
      if (response.ok) {
        alert('Book returned successfully!');
        // 重置表单
        this.bookid= "",
        this.userid= ""
      } else {
        alert('Failed to return book.');
      }
    }
  },
};
</script>